import s from "./LoginBlock.module.css";
import React from "react";
import {NavLink} from "react-router-dom";
import userPhoto from "../../../assets/images/user.jpg"

const LoginBlock = (props) => {


    if (!props.userProfile) {
        return null
    }
    return (
        <div className={s.loginBlockMainDiv}>
            <div className={s.loginBlock}>
                {props.isAuth
                    ? <NavLink to={`/profile`}>{props.login}
                        <button className={s.logoutButton} onClick={props.logoutUser}>Выйти</button>
                    </NavLink>
                    : <NavLink to={`/login`}>Login</NavLink>}
                <div className={s.topNavButton}>
                    <div className={s.userPhotoBlock}>
                        <img alt="" src={props.userProfile.photos.small || userPhoto}/>
                    </div>
                    <div>
                        <svg fill="none" height="8" viewBox="0 0 12 8" width="12" xmlns="http://www.w3.org/2000/svg">
                            <path clipRule="evenodd"
                                  d="M2.16 2.3a.75.75 0 011.05-.14L6 4.3l2.8-2.15a.75.75 0 11.9 1.19l-3.24 2.5c-.27.2-.65.2-.92 0L2.3 3.35a.75.75 0 01-.13-1.05z"
                                  fill="currentColor" fillRule="evenodd"/>
                        </svg>
                    </div>
                </div>
            </div>
            {/*<div className={s.userPanel}>
                <ul>
                    <li>AAA</li>
                    <li>BBB</li>
                    <li>CCC</li>
                    <li>DDD</li>
                </ul>
            </div>*/}
        </div>
    )
}

export default LoginBlock;
